/* stylelint-enable */
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

html {
  height: 100%; }

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  height: 100%;
  font-family: "Roboto", Arial, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #2c2f32; }

main {
  display: flex;
  flex-direction: column;
  width: 100%;
  flex: 1 0 auto;
  background-color: #EDEFF1; }

a {
  color: inherit;
  text-decoration: none; }

* {
  box-sizing: border-box; }

input:not([type="checkbox"]):not([type="radio"]) {
  appearance: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden; }

.button {
  padding: 0;
  border: none;
  background-color: transparent; }

.page-header {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  height: 59px;
  background: linear-gradient(90deg, #00417B 0%, #237CB1 50.26%, #2580B2 52.72%, #46D2CC 100%);
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.159392); }

.page-header__search {
  width: 179px;
  height: 100%;
  padding-top: 21px;
  padding-left: 20px;
  margin-right: 58px;
  background: url("../img/qr-code.svg") left 20px center no-repeat, url("../img/header-search-bg.svg") center no-repeat;
  box-shadow: 1px 0px 1px rgba(0, 0, 0, 0.20287); }

.page-header__search-button {
  width: 80px;
  height: 20px;
  margin-left: 51px;
  padding-left: 28px;
  border: none;
  color: #2b2b2b;
  text-transform: uppercase;
  background-color: transparent;
  background-image: url("../img/loupe.svg");
  background-repeat: no-repeat;
  background-position: left center;
  background-size: 23%; }

.page-header__new-action-button {
  width: 42px;
  height: 42px;
  margin-top: 9px;
  margin-right: 308px;
  background: url("../img/circle.svg") center no-repeat; }

.page-header__notifications-button {
  width: 25px;
  height: 25px;
  margin-top: 17px;
  margin-right: 45px;
  background: url("../img/bell.svg") center no-repeat; }

.page-header__vendor-name-button {
  width: 240px;
  height: 100%;
  color: #ffffff;
  text-transform: uppercase;
  background: url("../img/house.svg") left center no-repeat; }

.payment-order {
  width: 900px;
  background-color: #ffffff;
  margin-left: auto;
  height: 100%;
  box-shadow: -8px 9px 25px rgba(49, 68, 93, 0.23112); }

.site-navigation {
  display: flex;
  height: 100%;
  margin-right: 127px;
  align-items: center;
  flex-wrap: wrap; }

.site-navigation__item {
  color: #ffffff;
  opacity: 0.8;
  text-transform: uppercase; }

.site-navigation__item:not(:last-of-type) {
  margin-right: 36px; }
